

















import { computed, Data } from '@nuxtjs/composition-api'
import { timeFormat } from '@/plugins/timeFormat'

export default {
  name: 'ArticleCard',
  props: {
    item: {
      type: Object as any,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: {},
    },
  },
  setup(props: Data) {
    const time = computed(() => {
      return timeFormat((props.item as any).updated)
    })

    return { time }
  },
}
